export function isDefined(value: string | number | boolean | undefined): boolean {
  return value !== undefined;
}

export function attrBoolean(value: boolean | undefined, validFalse = true): string | undefined {
  if (value === undefined || value === null) {
    return undefined;
  }

  if (value || validFalse ) {
    return value.toString();
  }
  
  return undefined;
}

export function attrNumber(value: number | undefined): string | undefined {
  if (value !== undefined && value !== null) {
    return value.toString();
  }
  return undefined;
}

export function attrString(value: string | undefined | null, validEmpty = false): string | undefined {
  if (value?.length > 0) {
    return value;
  }

  if (validEmpty && String(value).trim().length === 0) {
    return '';
  }

  return undefined;
}
